<template>
  <div class='rightList'>
      <div class='rightTop'>
          <img class='rightTop_img' src="./img/4.jpg" >
      </div>
      <div class='rightNotice'>
          <div class='rightNotice_head'>
              <ul class='tab_a'>
                  <li class='tab_b' v-on:mouseenter.stop="getMAppear()">
                      <span class='rightTop_span' >信息公告</span>
                  </li>
                  <li class='tab_b' v-on:mouseenter.stop="getSAppear()">
                      <span class='rightTop_span' >服务公告</span>
                  </li>
              </ul>
          </div>
          
          <div class='rightNotice_data'>
              <div class='data_ message' v-bind:style="{display:condition1}">
                  <div class='data_logo'>
                      <ul>
                          <li class='right_li'
                          v-for="(i,index) in rightData" :key='index'>
                          <span style="color:#ff2832" class='right_span'>{{rightData[index]}}</span>
                          </li>
                      </ul>
                  </div>
              </div>
              <div class='data_serve'   v-bind:style="{display:condition2}">
                  <div class='data_logo'>
                      <ul>
                          <li class='right_li'
                          v-for="(i,index) in rightServe" :key='index'>
                          <span  class='right_span'>{{rightServe[index]}}</span>
                          </li>
                      </ul>
                  </div>
              </div>
          </div>
      </div>
      <div class='rightFooter'>
         <div class="swiper-container"  id='rightList' >
            <div class="swiper-wrapper">
                <div class="swiper-slide" 
                      v-for="(z,index3) in rightPicture"
                      :key="index3"
                >
                    <img :src="getPUrl(index3)" alt="">
                </div>
                
             </div>
             <!-- 如果需要分页器 -->
             <div class="swiper-pagination"></div>
             
             
             
             
         </div>

      </div>
  </div>
</template>

<script>
import Swiper from 'swiper';
export default {
name:'rightList',
data(){
    return {
         rightData:['阅读盛宴 图书每满100减50','阅读盛宴 爆品童书抢购','书香节食品会场最高300-210','阅读盛宴 科技图书每满100减50','文艺社科经管生活好书每满100减50'],
         rightServe:['关于谨防诈骗的重要提示','话费卡兑换当当礼品卡','多种商品，礼品卡支付更优惠！','【虚假发货】-处罚公告'],
         condition1:'block',
         condition2:'none',
         rightPicture:['http://img58.ddimg.cn/244400184737498_y.jpg','http://img61.ddimg.cn/upload_img/00886/418bpqgpc/yctabqian202x119-1650012875.jpg','http://img60.ddimg.cn/upload_img/00549/youxiaxiaobanner/qixianshengmiaoxiaojie2-1559267193.jpg'],

   }
},
methods:{
  getMAppear:function(){
      if(this.condition1=='none'){
          this.condition1='block';
          this.condition2='none';
      }
  },
  getSAppear:function(){
      if(this.condition2=='none'){
          this.condition1='none';
          this.condition2='block';
      }
      
  },
  getPUrl(index3){
       return this.rightPicture[index3];
  },
  
},
watch: {
      list: {
      
         immediate: true,
         handler() {
        
              this.$nextTick(() => {
              var rightSwiper = new Swiper(document.getElementById('rightList'), { // eslint-disable-line no-unused-vars
                    autoplay:true,
            
                   pagination: {
                          el: ".swiper-pagination",
              
                         
                      },
          
                   observer:true,
                   observeParents:true,

                   navigation: {
                   nextEl: ".swiper-button-next",
                   prevEl: ".swiper-button-prev",
                      },
               });

            });
          },
       },
    },
}
</script>

<style>
.rightList{
    width: 202px;
    border-bottom: 1px solid #e6e6e6;
    border-right: 1px solid #e6e6e6;
}
.rightTop{
    height: 194px;
    width: 202px;
    border-top: 1px solid #e6e6e6;
}
.rightNotice{
    width: 202px;
    overflow: hidden;
    height: 173px;
}
.rightFooter{
    width: 202px;
    height: 119px;
    position: relative;
}
.rightTop_img{
    display: block;
    width: 202px;
    height: 195px;
}

.tab_a{
    border-top: 1px solid #e6e6e6;
    height: 29px;
    overflow: hidden;
    width: 204px;
}
.tab_b{
    margin-left: -1px;
    float: left;
    width: 101px;
    border: 1px solid #e6e6e6;
    border-top: 0;
    height: 28px;
    background-color: #f0f0f0;
    cursor: default;
    font: 14px/29px "Microsoft Yahei";
    text-align: center;
    color: #646464;
    overflow: hidden;
}
.right_li{
    line-height: 25px;
    padding-left: 10px;
    width: 183px;
    height: 25px;
    background: url(http://img60.ddimg.cn/upload_img/00111/home/home_sprite_1507.png) no-repeat 10px -188px;
    overflow: hidden;
}
.right_span{
    display: block;
    padding-left: 10px;
    color: #646464;
    font-family: "Microsoft Yahei";
}
</style>