import Vue from 'vue'
import Vuex from 'vuex'
import { nanoid } from 'nanoid'
//const {nanoid}=require('nanoid')

Vue.use(Vuex)

const store=new Vuex.Store({
   state:{
       carList:[]
   },
   mutations:{
       addCart(state,payload){
           let oldMeta = state.carList.find(i=>i.img==payload.img)

           if(oldMeta){
               oldMeta.count+=1
               console.log('数量+1')
           }else{
               payload.count=1
               payload.id=nanoid()
               state.carList.push(payload)
               alert('添加成功')
           }  
           
          
       },
       delate(state){
        for(let i=0;i<state.carList.length;i++){
            if(state.carList[i].count==0){
                state.carList.splice(i,i+1);
            }
        }
       },
       delateAll(state,link2){
           console.log('ok')
        for(let j=0;j<state.carList.length;j++){
            if(state.carList[j].img==link2){
                state.carList.splice(j,j);
            }
        }
       }
   }

})

export default  store