<template>
  <div class='centerList'>
      <div class='centerList_head'>
          <h3 class='centerList_h3'>
              <span class='centerList_span'>厂商周</span>
          </h3>
      </div>
      <div class='centerList_list'>
          <div class="swiper-container"  id='centerList'>
            <div class="swiper-wrapper">
                <div class="swiper-slide" >
                    <img v-for="(u1,index) in img1Url" :key="index" 
                     :src="getImg1Url(index)" alt="">
                </div>
                <div class="swiper-slide" >
                    <img v-for="(u1,index) in img2Url" :key="index" 
                     :src="getImg2Url(index)" alt="">
                </div>
                
                
             </div>
             <!-- 如果需要分页器 -->
             <div class="swiper-pagination"></div>
             
             <!-- 如果需要导航按钮 -->
             <div class="swiper-button-prev"></div>
             <div class="swiper-button-next"></div>
             
            
         </div>
      </div>
  </div>
</template>

<script>
import {getHomeData} from './centerList';
import Swiper from 'swiper';
export default {
  name:'centerList',
  data(){
      return{
          img1Url:[],
          img2Url:[],
          
      }
  },
  created() {
   getHomeData().then(res => {
           this.img1Url=res.data.img1Url;
           this.img2Url=res.data.img2Url;
     })
  },
  watch:{
      list:{
          immediate: true,
          handler(){
              this.$nextTick(() => {
              var mySwiper = new Swiper(document.getElementById("centerList"), { // eslint-disable-line no-unused-vars
                    autoplay: {
                        delay: 3000,
                        stopOnLastSlide: false,
                         disableOnInteraction: false,
                   },
                   pagination: {
                          el: ".swiper-pagination",
              
                         
                      },
          
                   observer:true,
                   observeParents:true,

                   navigation: {
                   nextEl: ".swiper-button-next",
                   prevEl: ".swiper-button-prev",
                      },
               });
              });
          }
      }
  },
  methods:{
    getImg1Url(index){
        return this.img1Url[index];
    },
    getImg2Url(index){
        return this.img2Url[index];
    }
  },
}
</script>

<style>
.centerList_span{
    color: #ff2832;
    font: 20px "Microsoft YaHei",Simsun;
}
.centerList{
    float: left;
    width: 204px;
    overflow: hidden;
    position: relative;
}
.centerList_head{
    height: 34px;
    line-height: 34px;
    border-bottom: 2px solid #ff2832;
    position: relative;
    overflow: hidden;
    padding-top: 5px;
}
.centerList_h3{
   float: left;
    color: #ff2832;
    font: 20px "Microsoft YaHei",Simsun;
    text-decoration: none;
    cursor: pointer;
    padding-left: 8px;
    width: 120px;
    height: 28px;
    overflow: hidden;
}
.centerList_list{
    width: 203px;
    overflow: hidden;
    border-right: 1px solid #e6e6e6;
}

</style>