<template>
  <div class='home_miaosha'>
      <div class='home_head'>
          <div class='time'>
              <a href="" class='other'><img class='time_img' src="http://img62.ddimg.cn/upload_img/00782/home/home_miaosha_title-1561107108.png" alt=""></a>
              <span class='sec'></span>
              <span class='sec'></span>
              <span class='sec'></span>
          </div>
          <ul class='home_time'>
              <li class='time_list'>
                  <a href="" class='time_a'>00:00场</a>
              </li>
              <li class='time_list'>
                  <a href="" class='time_a'>08:00场</a>
              </li>
              <li class='time_list'>
                  <a href="" class='time_a'>12:00场</a>
              </li>
              <li class='time_list'>
                  <a href="" class='time_a'>16:00场</a>
              </li>
              <li class='time_list'>
                  <a href="" class='time_a'>20:00场</a>
              </li>
          </ul>
      </div>
      <div class='home_list'>
          <div class='info'
               v-for="(u,index) in imgUrl"
               :key="index"
          >
              <a href="" class='pic'><img class='list_img' :src="getUrl(index)" alt=""></a>
              <div class='list_title'>{{listTitle[index]}}</div>
              <div class='list_price'>
                  ¥
                  <span class='price' style="color:#ff2832">{{listPrice[index]}}</span>
              </div>
          </div>
      </div>
  </div>
</template>

<script>
import {getHomeData} from './center';
export default {
    name:'center',
    data(){
        return {
          imgUrl:[],
          listPrice:[],
          listTitle:[],        
        }
    },
    methods:{
       getUrl(index){
           return this.imgUrl[index];
       },
    },
    created() {
     getHomeData().then(res => {
           this.imgUrl=res.data.listUrl;
           this.listPrice=res.data.listPrice;
           this.listTitle=res.data.listTitle;
     })
   },
}
</script>

<style>

.info{
    float: left;
    width: 198px;
    height: 227px;
    border-right: 1px solid #e6e6e6;
    border-bottom: 1px solid #e6e6e6;
    overflow: hidden;
    position: relative;
}
.pic{
    display: block;
    width: 150px;
    height: 150px;
    overflow: hidden;
    margin: 10px auto 0;
}
.list_img{
    width: 150px;
    height: 150px;
}
.list_title{
    width: 180px;
    overflow: hidden;
    line-height: 22px;
    height: 22px;
    margin: 0 auto;
    padding-top: 15px;
    color: #8c8c8c;
    font-size: 12px;
    text-decoration: none;
}
.list_price{
    color: #ff2832;
    width: 180px;
    margin: 2px auto 0;
    overflow: hidden;
    line-height: 20px;
    height: 20px;
    font-family: Arial;
}
.price{
    font-size: 16px;
    margin-left: 5px;
    position: relative;
    top: 1px;
}
.home_head{
    height: 39px;
    border-bottom: 2px solid #3c3c3c;
    position: relative;
}
.other{
    position: absolute;
    display: block;
    left: 0;
    top: 0;
    width: 83px;
    height: 39px;
    overflow: hidden;
}

.time{
    float: left;
    background: url(http://img63.ddimg.cn/upload_img/00111/home/home_miaosha.png) no-repeat 83px 0;
    height: 39px;
    padding-left: 113px;
    font: 14px "Microsoft YaHei";
    position: relative;
}
.time_img{
    display: block;
    width: 64px;
    height: 39px
}
.sec{
    float: left;
    height: 24px;
    line-height: 24px;
    padding: 5px 11px 0 0;
    text-align: center;
    width: 26px;
    overflow: hidden;
}
.home_time{
    position: absolute;
    left:305px;
    top:0;
    
}
.time_list{
    width: 80px;
    text-align: center;
    height: 38px;
    float: left;
    margin-right: 50px;
}
.time_a{
    display: block;
    width: 80px;
    text-align: center;
    overflow: hidden;
    height: 38px;
    line-height: 38px;
    padding-bottom: 1px;
    font-size: 20px;
    font-weight: bold;
    text-decoration: none;
}
</style>