import {getHomeData} from './top';

export default {
    name:'Top',
    data(){
        return {
            message:'',
            search:[],
            newSearch:[],
            
        }
    },
    watch:{
        message:{
            handler(newVal,oldVal){
                this.newSearch=this.search.filter((item)=>{
                    if(item.indexOf(newVal)!=-1){
                        return item
                    }
                })
                console.log(oldVal)
                console.log(this.newSearch)
                
            },
             immediate:true,
             
        },
    },
    
    methods: {
        control(){
            if(this.message!='')
