        // changeR(index){
        //     this.run[index]=false
        // }
       
        goTo(){
            this.$router.push('/login');
        },
        goToBuy(){
            this.$router.push('/buy');
        }
    },
    created() {
   getHomeData().then(res => {
            this.search=res.data;
            console.log(this.search)
     })
    },
   
}
</script>

