<script>
import {getHomeData} from './clothing';
import Swiper from 'swiper';
export default {
name:'clothing',
data(){
  return {
      list:[],
      clothing:{
        upper:[],
        lower:[],
      },
      kid:{
        upper:[],
        lower:[],
      },
      shot:{
        upper:[],
        lower:[],
      },
      sport:{
        upper:[],
        lower:[],
      },
      underwear:{
        upper:[],
        lower:[],
      },
      Swiper:[],
      isShow1:true,isShow2:false,isShow3:false,isShow4:false,isShow5:false,
  }
},
watch: {
      list: {
      
         immediate: true,
         handler() {
        
              this.$nextTick(() => {

               var NewSwiper = new Swiper(document.getElementById("meta1"), { // eslint-disable-line no-unused-vars
                    effect:'fade',
                   autoplay: {
                        delay: 5000,
                        stopOnLastSlide: false,
                         disableOnInteraction: false,
                   },
                   pagination: {
                          el: ".swiper-pagination",
                          clickable :true,
                         
                      },
          
                   observer:true,
                   observeParents:true,

                   navigation: {
                   nextEl: ".swiper-button-next",
                   prevEl: ".swiper-button-prev",
                      },
               });
               var NewSwiper1 = new Swiper(document.getElementById("meta2"), { // eslint-disable-line no-unused-vars
                    effect:'fade',
                   autoplay: {
                        delay: 5000,
                        stopOnLastSlide: false,
                         disableOnInteraction: false,
                   },
                   pagination: {
                          el: ".swiper-pagination",
                          clickable :true,
                         
                      },
          
                   observer:true,
                   observeParents:true,

                   navigation: {
                   nextEl: ".swiper-button-next",
                   prevEl: ".swiper-button-prev",
                      },
               });
               var NewSwiper2 = new Swiper(document.getElementById("meta3"), { // eslint-disable-line no-unused-vars
                    effect:'fade',
                   autoplay: {
                        delay: 5000,
                        stopOnLastSlide: false,
                         disableOnInteraction: false,
                   },
                   pagination: {
                          el: ".swiper-pagination",
                          clickable :true,
                         
                      },
          
                   observer:true,
                   observeParents:true,

                   navigation: {
                   nextEl: ".swiper-button-next",
                   prevEl: ".swiper-button-prev",
                      },
               });
               var NewSwiper3 = new Swiper(document.getElementById("meta4"), { // eslint-disable-line no-unused-vars
                    effect:'fade',
                   autoplay: {
                        delay: 5000,
                        stopOnLastSlide: false,
                         disableOnInteraction: false,
                   },
                   pagination: {
                          el: ".swiper-pagination",
                          clickable :true,
                         
                      },
          
                   observer:true,
                   observeParents:true,

                   navigation: {
                   nextEl: ".swiper-button-next",
                   prevEl: ".swiper-button-prev",
                      },
               });
               var NewSwiper4 = new Swiper(document.getElementById("meta5"), { // eslint-disable-line no-unused-vars
                    effect:'fade',
                   autoplay: {
                        delay: 5000,
                        stopOnLastSlide: false,
                         disableOnInteraction: false,
                   },
                   pagination: {
                          el: ".swiper-pagination",
                          clickable :true,
                         
                      },
          
                   observer:true,
                   observeParents:true,

                   navigation: {
                   nextEl: ".swiper-button-next",
                   prevEl: ".swiper-button-prev",
                      },
               });

            });
          },
       },
    },

created() {
   getHomeData().then(res => {
          this.Swiper=res.data.url.swiper;
          
         this.list=res.data.url.list;
         this.clothing.upper=res.data.url.listUrl.clothing.upper;
         this.clothing.lower=res.data.url.listUrl.clothing.lower;
         this.kid.upper=res.data.url.listUrl.kid.upper;
         this.kid.lower=res.data.url.listUrl.kid.lower;
         this.sport.upper=res.data.url.listUrl.sport.upper;
         this.sport.lower=res.data.url.listUrl.sport.lower;
         this.underwear.upper=res.data.url.listUrl.underwear.upper;
         this.underwear.lower=res.data.url.listUrl.underwear.lower;
         this.shot.upper=res.data.url.listUrl.shot.upper;
         this.shot.lower=res.data.url.listUrl.shot.lower;
        
     })
  },
methods:{
  getSwiper(index){
        return this.Swiper.swiper1[index]
  },
  getSwiper1(index){
        return this.Swiper.swiper2[index]
  },
  getSwiper2(index){
        return this.Swiper.swiper3[index]
  },
  getSwiper3(index){
        return this.Swiper.swiper4[index]
  },
  getSwiper4(index){
        return this.Swiper.swiper5[index]
  },
  getUrl(index1){
    return this.clothing.upper[index1];
  },
  get1Url(index1){
    return this.clothing.lower[index1];
  },
  get2Url(index2){
    return this.kid.upper[index2];
  },
  get3Url(index2){
    return this.kid.lower[index2];
  },
  get4Url(index3){
    return this.sport.upper[index3];
  },
  get5Url(index3){
    return this.sport.lower[index3];
  },
  get6Url(index4){
    return this.underwear.upper[index4];
  },
  get7Url(index4){
    return this.underwear.lower[index4];
  },
  get8Url(index5){
    return this.shot.upper[index5];
  },
  get9Url(index5){
    return this.shot.lower[index5];
  },
  get1Appear(){
     this.isShow1=true;
     this.isShow2=false;
     this.isShow3=false;
     this.isShow4=false;
     this.isShow5=false;
  },
  get2Appear(){
     this.isShow1=false;
     this.isShow2=true;
     this.isShow3=false;
     this.isShow4=false;
     this.isShow5=false;
  },get3Appear(){
     this.isShow1=false;
     this.isShow2=false;
     this.isShow3=true;
     this.isShow4=false;
     this.isShow5=false;
  },get4Appear(){
     this.isShow1=false;
     this.isShow2=false;
     this.isShow3=false;
     this.isShow4=true;
     this.isShow5=false;
  },get5Appear(){
     this.isShow1=false;
     this.isShow2=false;
     this.isShow3=false;
     this.isShow4=false;
     this.isShow5=true;
  },
},
}
</script>

