<template>
<div>
  <div class='w960' id='cart' v-show='this.$store.state.carList!=null'>
      <div class='fn_shop' >
          <div class='shop_buy'>
             <div class='shop_list'>
                 <table class='shop_table'>
                     <tbody
                     v-for='(s,index) in this.$store.state.carList'
                     :key="index"
                     >
                         <tr class='tree_top'>
                             <td class='row1 listing'></td>
                             <td class='row_img listing'>
                                 <a href=""><img :src="getImg(index)" alt=""></a>
                             </td>
                             <td class='row_name listing'>
                                 <div class='buy_name'>
                                     <a href="" class='buy_name_a'>
                                     {{$store.state.carList[index].introduce}}
                                     </a>
                                     </div>
                             </td>
                             <td class='row3 listing'>
                                 <span>¥{{$store.state.carList[index].price}}</span>
                             </td>
                             <td class='row_count row3 listing'>
                                 <span class='amount'>
                                     <button class='but1' @click="reduce(index)">-</button>
                                     <input type="text" class='product_count' :value="getCount(index)">
                                     <button class='but1' @click="add(index)">+</button>
                                 </span>
                             </td>
                             <td class='row4 listing'>
                                 <span class='red'>¥{{$store.state.carList[index].price}}</span>
                             </td>
                             <td class='row5 listing'>
                                 <button class='delate' @click="delateAll(index)">删除</button>
                             </td>
                         </tr>
                         <tr class='tree_last'></tr>
                     </tbody>
                     <tfoot>
                         <tr class='total'>
                             <td class='row1'>&nbsp;</td>
                             <td class='row_img'>店铺合计</td>
                             <td class='row2'>&nbsp;</td>
                             <td class='row4'>
                                 <span class='number'></span>
                             </td>
                         </tr>
                     </tfoot>
                 </table>
             </div>
          </div>
      </div>
  </div>
  <div class='buyCar'>
      <div class='empty' v-show="this.$store.state.carList==''"></div>
      <div class='shop'>
          <div class='ads_tab'></div>
          <div class='ads_bos'>
              <div class='showList'>
                  <div class='bos'>
                      <ul class='bos_ul'>
                          <li class='bos_li' 
                          v-for="(y,index) in url"
                          :key='index'
                          >
                          <!-- v-on:mouseenter.stop='appear(index)'
                          v-on:mouseleave.stop='leave(index)' -->
                          
                              <a  class='bos_a'><img :src="getUrl(index)" alt=""></a>
                              <p class='bos_p'>{{introduce[index]}}</p>
                              <p class='bos_p'>
                                  <span class='price'>¥{{price[index]}}</span>
                              </p>
                              
                              <a  :class="{'active':active,'close':close}" href="#" class='add'
                             @click="addCar(index)"
                              
                              >加入购物车</a>
                          </li>
                      </ul>
                  </div>
              </div>
          </div>
      </div>

  </div>
</div>
</template>

<script>
import {getHomeData} from './buy'
export default {
   name:'buy',
   data(){
       return {
           url:[],
           introduce:[],
           price:[],
           active:true,
           close:false
       }
   },
   
  
   methods:{
       add(index){
            this.$store.state.carList[index].count++;
       },
       reduce(index){
            this.$store.state.carList[index].count--;
            if(this.$store.state.carList[index].count==0){
                this.$store.commit('delate')
            }
       },
       getCount(index){
           return this.$store.state.carList[index].count
       },
       getUrl(index){
           return this.url[index];
       },
       getImg(index){
          
           return this.$store.state.carList[index].img
       },
    //    appear(index){
          
          
    //       this.isShow[index]=true
    //        console.log(this.isShow)     
    //    },
    //    leave(index){
    //       this.isShow[index]=false
    //    },
       addCar(index){
          
         const meta = {}
             meta.img=this.url[index];
             meta.introduce=this.introduce[index];
             meta.price=this.price[index];

             this.$store.commit('addCart',meta)
         
       },
       delateAll(index){
          const link1=this.url[index]
          this.$store.commit('delateAll',link1)
       },
       

   },
   created() {
   getHomeData().then(res => {
           this.url=res.data.url
           this.introduce=res.data.name
           this.price=res.data.price
           
           
     })
  },
}
</script>

<style>
.delate{
    text-decoration: none;
    color: #323232;
    font-size: 12px;
}
.red{
    color: #ff2832;
}
.product_count{
    width: 36px;
    height: 28px;
    margin: 0;
    float: left;
    padding: 0;
    text-align: center;
    border: 1px solid #dcdcdc;
    border-width: 0 1px;
    background-color: #fff;
    font-weight: bold;
}

.but1{
    display: block;
    float: left;
    height: 28px;
    width: 28px;
    background-color: #f4f4f4;
}
.amount{
    border: 1px solid #dcdcdc;
    height: 28px;
    width: 94px;
    line-height: 28px;
    font-size: 14px;
    margin: 5px auto;
    overflow: hidden;
}
.buy_name_a{
    word-break: break-all;
    word-wrap: break-word;
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    font-size: 12px;
    background: transparent;
    list-style: none;
    text-decoration: none;
    color: #323232;
}
.buy_name{
    display: block;
    width: 100%;
    height: 36px;
    overflow: hidden;
    margin-bottom: 27px;
}
.listing{
    vertical-align: top;
    padding-top: 15px;
    position: relative;
    line-height: 22px;
    text-align: center;
    border-bottom: 1px solid #e4e4e4;
}
table {
    border-collapse: separate;
    text-indent: initial;
    border-spacing: 2px;
}
.row5{
    width: 70px;
}
.row4{
    color: #323232;
    width: 130px;
}
.row3{
    width: 130px;
    vertical-align: top;
    padding-top: 15px;
    position: relative;
    line-height: 22px;
    text-align: center;
}
.row_name{
        width: 330px;
    line-height: 18px;
    text-align: left;
    position: relative;
    padding-top: 17px;
}
.row_img{
    vertical-align: top;
    text-align: center;
    padding: 20px 20px 20px 0;
    width: 80px;
    border-bottom: 0;
    position: relative;
    z-index: 9;
    font-size: 0;
    line-height: 0;
}
.row1{
    padding: 0 0 0 10px;
    width: 42px;
    vertical-align: middle;
}
td{
    border-bottom: none 0;
}

tr,tbody,table,tfoot,td,button{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    font-size: 100%;
    background: transparent;
    list-style: none;
}
tbody{
    display: table-row-group;
    vertical-align: middle;
}
.shop_list{
    width: 958px;
    border: 1px solid #ebebeb;
    background-color: #fafafa;
    margin-bottom: 40px;

}
.w960{
    width: 960px;
    margin: 0 auto;
   
}
.active{
    display: block;
}
.close{
    display: none;
}
.add{
    position: absolute;
    top: 136px;
    left: 10px;
    width: 150px;
    height: 24px;
    line-height: 24px;
    background-color: #ff5e5e;
    color: #fff;
    opacity: .85;
}
.price{
    font-size: 16px;
    color: #ff2832;
    line-height: 32px;
}
.bos_p{
    text-align: left;
    line-height: 14px;
    width: 150px;
    height: 28px;
    overflow: hidden;
    color: #323232;
}
.bos_a{
    display: inline-block;
    width: 150px;
    height: 150px;
    margin: 10px 0;
}
.bos_li{
    width: 150px;
    border: none 0;
    padding: 0 10px 12px;
    margin: 0 20px 0 0;
    text-align: center;
    float: left;
    color: #323232;
    line-height: 16px;
    position: relative;
    overflow: hidden;
}
.bos_ul{
   width: 950px;
    float: left;
}
.showList{
    padding: 5px 4px 0 14px;
    width: 940px;
    overflow: hidden;
}
.ads_bos{
    position: relative;
}
.ads_tab{
    line-height: 36px;
    height: 36px;
    padding-left: 25px;
    font-weight: bold;
    font-size: 16px;
    color: #323232;
}
.buyCar{
    width: 960px;
    margin: 0 auto;
}
.shop{
    border: 1px solid #dcdcdc;
    margin-bottom: 20px;
    padding: 10px 0 15px;
}

.empty{
    padding: 90px 0 0 495px;
    background: url(http://shopping.dangdang.com/shoppingcart/images/empty.png) 90px 46px no-repeat;
    height: 190px;
    border: 1px  solid red;
}
</style>
