                 name:[],
                 price:[],
             },

             list1:[],
             list2:[],
             swiper:[],
             picture:[],   
             condition1:'block',
             condition2:'none',
             condition3:'none',
             condition4:'none',

             rank1:{
                 url:[],
                 name:[],
                 introduce:[],
             },
             rank2:{
                 url:[],
                 name:[],
                 introduce:[],
             }
              
        }
    },
    created() {
        getHomeData().then(res => {
           
           this.list1=res.data.url.list.list1;
           this.list2=res.data.url.list.list2;
           this.book1.url=res.data.url.bookUrl.picture.list1;
           this.book2.url=res.data.url.bookUrl.picture.list2;
           this.book3.url=res.data.url.bookUrl.picture.list3;
           this.book4.url=res.data.url.bookUrl.picture.list4;
           this.book1.name=res.data.url.bookUrl.name.list1;
           this.book2.name=res.data.url.bookUrl.name.list2;
           this.book3.name=res.data.url.bookUrl.name.list3;
           this.book4.name=res.data.url.bookUrl.name.list4;
           this.book1.price=res.data.url.bookUrl.price.list1;
           this.book2.price=res.data.url.bookUrl.price.list2;
           this.book3.price=res.data.url.bookUrl.price.list3;
           this.book4.price=res.data.url.bookUrl.price.list4;
           
           this.rank1.url=res.data.url.rank1.book.url;
           this.rank1.name=res.data.url.rank1.book.name;
           this.rank1.introduce=res.data.url.rank1.book.introduce;
           this.rank2.url=res.data.url.rank1.child.url;
           this.rank2.name=res.data.url.rank1.child.name;
           this.rank2.introduce=res.data.url.rank1.child.introduce;
           
     })
  },
  methods:{
      getBookUrl(index){
          return this.book1.url[index];
      },
      getBook2Url(index){
          return this.book2.url[index];
      },
      getBook3Url(index){
          return this.book3.url[index];
      },
      getBook4Url(index){
          return this.book4.url[index];
      },
      getAppear1(){
         this.condition1='block';
         this.condition2='none'
         this.condition3='none'
         this.condition4='none'
      },
      getAppear2(){
         this.condition1='none';
         this.condition2='block'
         this.condition3='none'
         this.condition4='none'
      },
      getAppear3(){
