<template>
<div>
  <div class='dBig'>
       
         <div class="swiper-container"  id='cur'>
            <div class="swiper-wrapper">
                <div class="swiper-slide" 
                      v-for="(i,index) in picture"
                      :key="index"
                >
                    <img :src="getUrl(index)" alt="">
                </div>
                
             </div>
             <!-- 如果需要分页器 -->
             <div class="swiper-pagination"></div>
             
             <!-- 如果需要导航按钮 -->
             <div class="swiper-button-prev"></div>
             <div class="swiper-button-next"></div>
             
             <!-- 如果需要滚动条 -->
             <div class="swiper-scrollbar"></div>
         </div>

  </div>
  <div class='swiper2'>
      <div class="swiper-container"  id='meta'>
            <div class="swiper-wrapper">
                <div class="swiper-slide" >
                    <ul class='photoList'>
                      <li class='imgStyle'
                        v-for="(j,index1) in imgB"
                        :key="index1" 
                      ><img :src="getImgUrl(index1)" alt=""></li>
                    </ul>
                </div>
                <div class="swiper-slide" >
                    <ul class='photoList'>
                      <li  class='imgStyle'
                        v-for="(x,index2) in imgC"
                        :key="index2" 
                      ><img :src="getImgCUrl(index2)" alt=""></li>
                    </ul>
                </div>
                
             </div>
             <!-- 如果需要分页器 -->
             
             
             <!-- 如果需要导航按钮 -->
             <div class="swiper-button-prev"></div>
             <div class="swiper-button-next"></div>
             
            
             
      </div>
  </div>
</div>
</template>

<script>
import {getHomeData} from './swiper.js';
import Swiper from 'swiper';

export default {
 name:'swiper',
 data(){
   return {
        picture:[],
        imgB:[],
        imgC:[],
   }
 },
 created() {
   getHomeData().then(res => {
       
         for(var i=0;i<8;i++)
         {
           this.picture.push(res.data[0].picture[i].imgA);
         }
         
         for(var j=0;j<4;j++)
         {
           this.imgB.push(res.data[0].swiper[0].imgB[j]);
         }
         for(var x=0;x<4;x++)
         {
           this.imgC.push(res.data[0].swiper[1].imgC[x]);
         }

     })
 },
 
 
    methods:{
        getUrl(index){
          return this.picture[index];
        },
        getImgUrl(index1){
          return this.imgB[index1];
        },
        getImgCUrl(index2){
          return this.imgC[index2];
        }
    },
    watch: {
      list: {
      
         immediate: true,
         handler() {
        
              this.$nextTick(() => {
              var mySwiper = new Swiper(document.querySelector(".swiper-container"), { // eslint-disable-line no-unused-vars
                    effect:'fade',
                    autoplay: {
                        delay: 5000,
                        stopOnLastSlide: false,
                         disableOnInteraction: false,
                   },
                   pagination: {
                          el: ".swiper-pagination",
                         clickable :true,
                         
                      },
          
                   observer:true,
                   observeParents:true,

                   navigation: {
                   nextEl: ".swiper-button-next",
                   prevEl: ".swiper-button-prev",
                      },
               });

               var NewSwiper = new Swiper(document.getElementById("meta"), { // eslint-disable-line no-unused-vars
                    effect:'fade',
                   autoplay: {
                        delay: 5000,
                        stopOnLastSlide: false,
                         disableOnInteraction: false,
                   },
                   pagination: {
                          el: ".swiper-pagination",
                          clickable :true,
                         
                      },
          
                   observer:true,
                   observeParents:true,

                   navigation: {
                   nextEl: ".swiper-button-next",
                   prevEl: ".swiper-button-prev",
                      },
               });
            });
          },
       },
    },

}
</script>

<style >
.dBig{
  display: block;
    width: 796px;
    height: 326px;
}
.swiper2{
    width: 796px;
    height: 160px;
    overflow: hidden;
    position: relative;
}
.imgStyle{
  float: left;
}
.photoList{
  width: 796px;
  height: 160px;
}
img{
  width: 100%;
  height: 100%;
}
</style>