<template>
  <div class='kid'>
       <div class='commodity_top'>
              <div class='clear'></div>
              <div class='top_screen'><a href="" class='screen_a'>孕婴童</a></div>
              <ul class='common_ul'>
                  <li class='common_line' v-for='(a,index) in head' :key="index">
                      <p class='common_name'>
                          <a href="" class='common_title'>{{head[index]}}</a>
                      </p>
                  </li>
              </ul>
          </div>
          <div class='commodity_middle'>
              <div class='col left'>
                  <a href="" class='l_pic'
                  v-for='(u,index) in left'
                  :key="index"
                  ><img :src="getLUrl(index)" class='pic_img'></a>
              </div>
              <div class='col mid'><img :src="getMUrl(0)" class='over_img'></div>
              <div class='col right'>
                  <a href="" class='l_pic'
                  v-for='(u,index) in right'
                  :key="index"
                  ><img :src="getRUrl(index)" class='pic_img'></a>
              </div>
          </div>
          
          <div class='commodity_bottom'>
              <div class='roll_aa'>
                  <div class='over'>
                      <ul class='wid' >
                      <li class='over_li' 
                      v-for='(u,index) in listUrl'
                      :key="index"
                      >
                          <a href="" class='li_a'><img :src="getLL(index)" alt=""></a>
                      </li>
                      </ul>
                  </div>
              </div>
          </div>

  </div>
</template>

<script>
import {getHomeData} from './kid';
export default {
    name:'kid',
    data(){
        return {
            left:[],
            middle:[],
            right:[],
            listUrl:[],
            head:['童书畅销榜','童书3-6岁榜','童书7-10岁榜'],
        }

    },
    created() {
   getHomeData().then(res => {
           this.left=res.data.url.left;
           this.middle=res.data.url.middle;
           this.right=res.data.url.right;
           this.listUrl=res.data.list;  
     })
  },
  methods:{
     getLUrl(index){
         return this.left[index];
     },
     getMUrl(index){
         return this.middle[index];
     },
     getRUrl(index){
         return this.right[index];
     },
     getLL(index){
         return this.listUrl[index];
     }
  },
}
</script>

<style scoped>
.clear{
    clear: both;
}
.wid{
    width: 10000px;
}
.li_a{
    display: block;
    width: 109px;
    height: 44px;
    margin-left: 4px;
    overflow: hidden;
}
.over_li{
    float: left;
    width: 118px;
    height: 44px;
    margin-left: -1px;
    border-left: 1px dashed #dfdfdf;
}
.over{
    width: 1179px;
    overflow: hidden;
    margin: 10px auto 0;
}
.commodity_bottom{
    height: 64px;
    border: 1px solid #e6e6e6;
    border-top: 0;
    overflow: hidden;
    background-color: #f7f7f7;
    position: relative;
}
.pic_img{
    width: 199px;
    height: 170px;
}
.l_pic{
    float: left;
    width: 220px;
    height: 194px;
    overflow: hidden;
    border: 1px solid #e6e6e6;
    border-width: 0 1px 1px 0;
}
.right{
    left: 536px;
    width: 663px;
}
.mid{
   left: 221px;
   display: block;
    width: 314px;
    height: 389px;
}
.left{
    left: 0;
    width: 221px;
}
.col{
    float: left;
}
.commodity_middle{
    border: 1px solid #e6e6e6;
    border-width: 1px 0 0 1px;
    position: relative;
    width: 1210px;
    height: 390px;
    display: flex;
    
}
.common_line{
    float: left;
    padding: 0 30px;
    margin: 13px 0 0 -13px;
    background: url(http://img63.ddimg.cn/upload_img/00111/home/title_bg.png) no-repeat left 3px;
    font: 16px/20px "Microsoft YaHei";
}
.hot{
    font: 14px/20px "Microsoft YaHei";
    padding-left: 5px;
    color: #323232;
}
.common_title{
    font: bold 16px/20px "Microsoft YaHei";
    text-decoration: none;
    color: #323232;
}
.common_li{
    float: left;
    padding: 0 30px;
    margin: 13px 0 0 -13px;
    background: url(http://img63.ddimg.cn/upload_img/00111/home/title_bg.png) no-repeat left 3px;
    font: 16px/20px "Microsoft YaHei";
}
.common_ul{
    float: left;
    height: 40px;
    overflow: hidden;
}
.top_screen{
    float: left;
    margin: 9px 0 7px;
    padding: 0 20px 0 6px;
    font: bold 20px/24px "Microsoft YaHei";
    border-right: 1px solid #b7b7b7;
    background: #fff;
    color: #323232;
}
.commodity_top{
    height: 40px;
    font: bold 20px/24px "Microsoft YaHei";
    color: #323232;
}
.kid{
    width: 1200px;
    margin: 0 auto 20px;
    height: 448px;
    
}
