<template>
  <div class='recommend'>
      <h3 class='head_top1'>
          <span class='top1_span'>为你推荐</span>
      </h3>
      <div class='shop'>
          <div class='ads_tab'></div>
          <div class='ads_bos'>
              <div class='showList showList1'>
                  <div class='bos'>
                      <ul class='bos_ul'>
                          <li class='bos_li' 
                          v-for="(y,index) in url"
                          :key='index'
                          >
                          <!-- v-on:mouseenter.stop='appear(index)'
                          v-on:mouseleave.stop='leave(index)' -->
                          
                              <a  class='bos_a'><img :src="getUrl(index)" alt=""></a>
                              <p class='bos_p'>{{introduce[index]}}</p>
                              <p class='bos_p'>
                                  <span class='price'>¥{{price[index]}}</span>
                              </p>
                              
                             
                          </li>
                      </ul>
                  </div>
              </div>
          </div>
      </div>
  </div>
</template>

<script>
import {getHomeData} from './recommend'
export default {
    name:'recommend',
    data(){
       return {
          url:[],
           introduce:[],
           price:[],
       }
    },
    methods:{
         getCount(index){
           return this.$store.state.carList[index].count
       },
       getUrl(index){
           return this.url[index];
       },
       getImg(index){
          
           return this.$store.state.carList[index].img
       },
    },
   created() {
   getHomeData().then(res => {
           this.url=res.data.url
           this.introduce=res.data.name
           this.price=res.data.price
           
           
     })
  },
}
</script>

<style>
.showList1{
    width:1200px
}
.top1_span{
    height: 34px;
    color: #ff2832;
    font: bold 20px "Microsoft YaHei";
    border-bottom: 2px solid #ff3228;
    padding-left: 5px;
}
.recommend{
    width: 1200px;
    margin: 0 auto 30px;
}
.head_top1{
        height: 34px;
    color: #ff2832;
    font: bold 20px "Microsoft YaHei";
    border-bottom: 2px solid #ff3228;
    padding-left: 5px;
}
