<template>
  <div class='navd'>
    <ul class='nav_ul'>
        <li 
          v-for="(item,index) in result" 
          class='nav_li'
         :key="index"
        >
          <span class='nav_span'>{{result[index]}}</span>
        </li>
    </ul>
    <div class='navig'>
      <i></i>
      <div class='classify'>
        <div class='classify_name'></div>
        <ul class='navig_ul'>
        <li class='navig_li'></li>
        </ul>
      </div>
      
    </div>
    
  </div>
</template>

<script>
import {getHomeData} from './nav';
export default {
   name:'navd',
     data(){
     return {
       result:[],
     }
   },
   created() {
     getHomeData().then(res => {
           for(var i=0;i<15;i++){
                  this.result.push(res.data[i].classify);
           }
           console.log(res)
     })
   },
}
</script>

<style >
.nav_ul{
  width: 200px;
}
.nav_li{
    height: 30px;
    padding-left: 18px;
    color: #323232;
    font: 14px/30px "Microsoft Yahei";
    position: relative;
    vertical-align: top;
}
.nav_span{
  color: #323232;
  font-size: 14px;
}
</style>